@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
body {
 background-color: antiquewhite;
}

* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.blogList em {
  color: #808080;
  margin: 0;
}

.blogList button {
  color: #f2f2f2;
  background-color: #808080;
  border-color: #333;
  border-radius: 8px;
  margin: 0;
  display: block;
  margin-left: auto;
}

.blogList button:hover {
  color: #f2f2f2;
  background-color: #f1356d;
  border-color: #333;
  border-radius: 8px;
  margin: 0;
  display: block;
  margin-left: auto;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #808080;
}

.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* blog previews / list */
.blogPreview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
  background-color: beige;
}

.blogPreview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blogPreview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.blogPreview a {
  text-decoration: none;
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 8px;
  transform-style: preserve-3d;
  box-shadow:  0 0 5px 5px  rgba(0,0,0,0.1);
  border-style: solid;
  border-top-color: #808080;
  border-left-color: #808080;
  border-right-color: #333;
  border-bottom-color: #333;
  border-width: 5px;
  transition-duration: 200ms;
  background-color: beige;
  height: 150px;
  width: 150px;
  transform: rotateY(var(--rotate-y,0)) translateY(var(--translate-y,0));
  margin: 15px;
}

.card:hover {
  --translate-y: -2px;
  box-shadow:  0 0 5px 5px  rgba(0,0,0,0.3);
}

.card.flip {
  border-left-color: #333;
  border-right-color: #808080;
  --rotate-y: 180deg;
}

.card .front,
.card .back {
  position: absolute;
  padding: 10px 16px;
  backface-visibility: hidden;
}

.card .back {
  transform: rotateY(180deg);
}

